import React, { FC, UIEventHandler, useEffect, useState, UIEvent } from "react";
import { ICity } from "../../../../api/models";
import { CityApi } from "../../../../api/rest/city.api";
import ClicableDiv from "../../../commonComponent/clicableDiv/ClicableDiv";
import * as appAction from "../../../../store/app";
import "./SelectionCity.scss";
import { useDispatch } from "react-redux";

export interface INamedProps {
  name: string;
}

export interface ISectionCity {
  scrolled?(): void;
}

const Named: FC<INamedProps> = ({ name }) => {
  return <div className="letterBlock__letter">{name}</div>;
};

const SelectionCity: FC<ISectionCity> = ({ scrolled }) => {
  const dispatch = useDispatch();
  const [city, setCity] = useState<ICity[]>();

  const click = (name) => {
    dispatch(appAction.Actions.changeCountry(name));
    dispatch(appAction.Actions.closeModal());
  };

  useEffect(() => {
    CityApi.getCity().then((response) => setCity(response));
  }, []);
  return (
    <div className="citySection">
      <div className="citySection__body">
        {city?.map((item) => {
          return (
            <div className="letterBlock">
              <Named name={item.letter} key={item.id} />
              {item.city.map((city) => {
                return (
                  <ClicableDiv
                    name={city}
                    clickEvent={click}
                    key={item.letter + city}
                  />
                );
              })}
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default SelectionCity;
